























































import { Component, Vue } from "vue-property-decorator";
@Component({
  components: {},
  props: {
    provider: String
  }
})
export default class PubDialog extends Vue {
  private provider: string | undefined;

  public title = "";
  public url = "";
  public chainId = "";
  public gasPrice = "";
  public gasLimit = "";

  cancel() {
    this.$emit("hide");
  }

  publish() {
    this.$emit("publish");
  }
}
